export default {
	data() {
		return {
			articleList: [],
			isContent: true
		}
	},
	methods: {

	},
	onLoad() {
		this.$api.getArticleC().then(res => {
			this.articleList = res.result.result
			this.isContent = this.articleList.length !== 0 ? true : false
		})
	}
}
